import * as Three from "three";
import OrbitControls from "three-orbitcontrols";
import * as dg from "dat.gui";

//----IMPORTS: FILES
import imgMedHeight from "../../../Files/med-height.jpg";
import imgMedNormal from "../../../Files/med-normal.png";

class ThreeClass {
  /******************************\
    Private Methods
    \******************************/

  _bindEvents() {
    window.addEventListener("resize", this.resize);
  }

  _generateTerrain() {
    let terrainGeom = new Three.PlaneBufferGeometry(1500, 1500, 200, 200);
    let terrainMtrl = this.mtrlTerrain;
    let terrainMesh = new Three.Mesh(terrainGeom, terrainMtrl);
    terrainGeom.rotateX(-Math.PI / 2);

    console.log(terrainGeom.face);

    return terrainMesh;
  }

  _generateLights() {
    let lights = new Three.Group();

    var light = new Three.PointLight();
    light.position.set(0, 100, 0);
    light.intensity = 0.5;

    lights.add(light);

    var light = new Three.PointLight();
    light.position.set(500, 500, 0);
    light.intensity = 0.5;

    lights.add(light);

    return lights;
  }

  _generateFog() {
    return new Three.Fog(0xffffff, 0, 1500);
  }

  /******************************\
    Public Methods
    \******************************/

  constructor() {
    this.element.appendChild(this.renderer.domElement);

    this._bindEvents();
    this.sceneContent();

    this.camera.position.set(-50, 200, 500);
    this.camera.lookAt(this.scene.position);

    this.gui;
    this.gui.add(this.guiOptions, "toggleMap").onChange(v => {
      if (v) {
        this.mtrlTerrain.wireframe = false;
        this.mtrlTerrain.color = new Three.Color(0x009900);
        this.mtrlTerrain.opacity = 1;
      } else {
        this.mtrlTerrain.wireframe = true;
        this.mtrlTerrain.color = new Three.Color(0x000000);
        this.mtrlTerrain.opacity = 0.2;
      }
    });

    this.control;
    this.render();
    this.resize();
  }

  sceneContent() {
    this.scene.add(this._generateTerrain());
    this.scene.add(this._generateLights());
    this.scene.fog = this._generateFog();
  }

  render = () => {
    this.renderer.render(this.scene, this.camera);
    this.control.update();
    requestAnimationFrame(this.render);
  };

  resize = () => {
    this.camera.aspect = this.width / this.height;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(this.width, this.height);
  };

  /******************************\
    Getters
    \******************************/

  get camera() {
    if (this._camera) {
      return this._camera;
    }

    let camera = new Three.PerspectiveCamera(
      this.fov,
      this.width / this.height,
      1,
      10000
    );

    return (this._camera = camera);
  }

  get element() {
    return (
      this._element || (this._element = document.querySelector("#three-app"))
    );
  }

  get fov() {
    return 45;
  }

  get height() {
    return this.element.offsetHeight;
  }

  get width() {
    return this.element.offsetWidth;
  }

  get scene() {
    if (this._scene) {
      return this._scene;
    }

    let scene = new Three.Scene();
    scene.background = new Three.Color(0xbfd1e5);

    return (this._scene = scene);
  }

  get gui() {
    return this._gui || (this._gui = new dg.GUI());
  }
  get guiOptions() {
    return {
      toggleMap: false,
      flatShading: false
    };
  }

  get control() {
    return this._control || (this._control = new OrbitControls(this.camera));
  }

  get renderer() {
    if (this._renderer) {
      return this._renderer;
    }

    let renderer = new Three.WebGLRenderer({
      antialias: true
    });
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.shadowMap.enabled = true;

    return (this._renderer = renderer);
  }

  get mtrlTerrain() {
    if (this._mtrlTerrain) {
      return this._mtrlTerrain;
    }

    let textureLoader = new Three.TextureLoader();
    let displacementMap = textureLoader.load(imgMedHeight),
      normalMap = textureLoader.load(imgMedNormal);

    let mtrl = new Three.MeshPhongMaterial({
      color: 0x000000,
      emissive: 0,
      specular: 0,
      shininess: 0,
      side: Three.DoubleSide,

      displacementMap,
      displacementScale: 300,

      normalMap,
      normalScale: new Three.Vector2(0.5, 0.5),

      wireframe: true,
      opacity: 0.2,
      transparent: true
    });

    return (this._mtrlTerrain = mtrl);
  }
}

export default ThreeClass;



