import React from "react";
import contentData from "../../data/contentText.json";
import logoSVG from "../../Files/mdelogo.svg";
const text = t => t.split("\n").map(line => <p>{line}</p>);

const Intro = () => {
  return (
    <div className="section intro">
      <div className="overlay" />
      <div className="copy_intro">{text(contentData.intro)}</div>
      <img className="logo" src={logoSVG} />
      <div className="main_buttons">
        <div>Explore</div>
        <div>Archive</div>
        <div>About</div>
      </div>
    </div>
  );
};

export default Intro;



