import React from "react";

const Methodology = props => {
  const handleOnClose = () => {
    props.onClose();
    let $el = document.querySelector(".methodology");
    $el.classList.remove("open");
  };

  setTimeout(() => {
    let $el = document.querySelector(".methodology");
    $el.classList.add("open");
  }, 100);

  return (
    <div className="section methodology">
      <header>Methodology #{props.content.id} title</header>
      <div className="content">Methodology #{props.content.id} Content</div>
      <button onClick={handleOnClose}>Close</button>
    </div>
  );
};

export default Methodology;



