import React, { Component } from "react";
import * as Three from "three";

class ThreeApp extends Component {
  constructor() {
    super();
    this.state = {
      threeRender: null
    };
  }

  componentDidMount() {
    this.setState({ threeRender: new this.props.src() });
  }

  render() {
    console.log("threeApp renderd");
    return <div id="three-app" />;
  }
}

export default ThreeApp;



