import React, { Component } from "react";

class HUD extends Component {
  render() {
    console.log("Hud Render", this.props);
    return (
      <div id="HUD">
        <nav>
          <ul className="menu">
            <li onClick={() => this.props.handlePage("about")}>Inicio</li>
            <li onClick={() => this.props.handlePage("map")}>Mapa</li>
            <li onClick={() => this.props.handlePage("methodologies")}>
              Metodologías
            </li>
          </ul>
          <ul className="social">
            <li />
            <li />
            <li />
          </ul>
        </nav>
      </div>
    );
  }
}

export default HUD;

