import React, { Component } from "react";
import Methodology from "./Methodology";

class Methodologies extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }],
      index: null
    };
  }

  openCard = i => {
    this.setState({ index: i });
  };
  closeCard = () => {
    setTimeout(() => this.setState({ index: null }), 1000);
  };

  render() {
    let { index, data } = this.state;
    return (
      <div className="methodologies">
        <div className="section cards">
          {data.map(card => {
            let i = card.id;
            return (
              <div className="card" key={i} onClick={() => this.openCard(i)}>
                <h1>Methodology #{i}</h1>
                <p>Description Paragraph</p>
              </div>
            );
          })}
          {index ? (
            <Methodology onClose={this.closeCard} content={data[index - 1]} />
          ) : null}
        </div>
      </div>
    );
  }
}

export default Methodologies;



